import { Component, OnInit } from '@angular/core';
import { KeysPipe } from '../shared/keys.pipe';
import { Http } from '@angular/http';
import { Router } from '@angular/router';
import { DataService } from '../shared/data.service';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { Observable } from 'rxjs/Rx'


@Component({
  selector: 'recommendation',
  templateUrl: './recommendation.component.html'
})
export class RecommendationComponent implements OnInit {

  items: any;
  ticks = 0;
  colors = ['#F7941D', '#FFFFFF', '#01233F'];
  showFillFormBtn:any='';
  constructor(private router: Router, private ds: DataService, private slimLoadingBarService: SlimLoadingBarService) {

    setTimeout(() => {
      this.slimLoadingBarService.complete();
      this.showFillFormBtn='Fill the form again';

      this.items = this.ds.getData();
      if (!this.items || '')
        router.navigate(['']);
    }, 2000);
  }

  navToDashboard() {
    this.router.navigate(['']);
    window.location.reload();

  }

  integrateData() {

  }
  ngOnInit() {
    // Start the loader here!
    this.slimLoadingBarService.start();
  }

}


